import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);//使用插件vuex
import ani from "./moudles/ani";
import refreshMore from "./moudles/refreshMore";
export default new Vuex.Store({
	state:{//状态，写数据
	},
	getters:{//得到，函数
	},
	mutations:{//变更，函数
	},
	actions:{//动作，函数
	},
	modules:{//模块
		ani,
		refreshMore,
		app:{//app指模块名称，自定义
			state:{//状态，写数据
				count:0
			},
			getters:{//得到，函数
			},
			mutations:{//变更，函数;同步操作;commit触发；注意命名commit提交的函数名相同的都会触发
			
			},
			actions:{//动作，函数;异步操作;dispatch触发
				
			},
			modules:{//子模块
			}
		},
		list:{//app指模块名称，自定义
			state:{//状态，写数据
				count:10
			},
			getters:{//得到，函数

			},
			mutations:{//变更，函数;同步操作;commit触发
				add(state,payload){//state指状态对象；payload指commit提交的数据
					state.count++;
					console.log(payload);
				},
				reduce(state,payload){
					state.count--;
					console.log(payload);
				}
			},
			actions:{//动作，函数;异步操作;dispatch触发
				add(context,payload){//语境
					console.log(context);
					context.commit("add",payload);
				},
				reduce(context,payload){//语境
					console.log(context);
					context.commit("reduce",payload);
				}
			},
			modules:{//子模块

			}
		}
	}
});


